@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

body {
  background-color: white;
}

.ProductDetails {
  /* background-color: rgb(255, 0, 0); */
  width: 100vw;
  max-width: 100%;
  padding: 0vh 2vw;
  box-sizing: border-box;
  display: flex;
  /* margin-top: 8vh; */
}

.ProductDetails > div {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding: 2vmax;
  box-sizing: border-box;
  /* border: 1px solid white; */
  /* background-color: aqua; */
}

.ProductDetails > div:last-child {
  align-items: flex-start;
}

.imageContainer {
  position: relative;
  /* background-color: black; */
}

.imagePreviewContainer {
  position: absolute;
  left: 0;
  margin-left: 6.5vw;
  /* background-color: blue; */
}

.carouselContainer1 {
  /* background-color: black; */
  /* margin-bottom: 13vh; */
}

.carouselContainer {
  position: absolute;
  margin-left: 10vw;
}

.imagePreview {
  display: flex;
  height: 7vmax;
  width: 7vmax;
  /* margin-top: 10px; */
  object-fit: scale-down;
}

.CarouselImage {
  height: 100%;
  width: 100%;
  float: right;
  object-fit: scale-down;
}

.rightProductDetails{
  width: 100%;
}

.detailsBlock-1 > h2 {
  color: rgb(54, 54, 54);
  margin-top: 50px;
  font-size: 35px;
}

.detailsBlock-2 {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 70%;
  margin-top: 7px;
  margin-left: -3px;
}

.detailsBlock-2-span {
  font: 200 0.8vmax cursive;
  color: rgba(0, 0, 0, 0.699);
}

.detailsBlock-3 {
  width: 100%;
  margin-top: 10px;
}

.detailsBlock-3 > h1 {
  color: rgba(17, 17, 17, 0.795);
  font: 400 1.8vmax "Franklin Gothic Medium";
  margin: 1vmax 0;
  padding-bottom: 40px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.205);
  width: 92%;
}
.detailsBlock-3-1 {
  position: relative;
  display: flex;
  align-items: center;
}

.detailsBlock-3-1-1 > p {
  font-size: 15px;
  color: gray;
  margin-bottom: 7px;
}

.detailsBlock-3-1-1 > button {
  position: absolute;
  border: 1px solid;
  color: black;
  background-color: white;
  padding: 0.5vmax;
  cursor: pointer;
  transition: all 0.5s;
  width: 55px;
  height: 50px;
  font-size: 23px;
  align-items: center;
}
.detailsBlock-3-1-1 > button:hover {
  border: 2px solid;
}

.detailsBlock-3-1-1 > input {
  /* border: none; */
  margin-left: 64px;
  margin-right: 10px;
  padding: 0.5vmax;
  width: 55px;
  height: 50px;
  text-align: center;
  font: 400 0.8vmax "Roboto";
  color: rgba(0, 0, 0, 0.74);
}

.addCartBtn {
  border: 1px solid;
  cursor: pointer;
  width: 92%;
  color: white;
  background: black;
  transition: all 0.5s;
  padding: 0.8vmax 2vmax;
  margin-top: 40px;
  outline: none;
  align-items: center;
  font-size: 15px;
}

.addCartBtn:hover {
  background-color: rgb(235, 232, 232);;
  color: black;
}

.BuyNowBtn {
  border: 1px solid;
  cursor: pointer;
  width: 92%;
  color: white;
  background: black;
  transition: all 0.5s;
  padding: 0.8vmax 2vmax;
  margin-top: 10px;
  outline: none;
  align-items: center;
  font-size: 15px;
}

.BuyNowBtn:hover {
  background-color: rgb(235, 232, 232);;
  color: black;
}


.detailsBlock-3 > p {
  /* border-top: 1px solid rgba(0, 0, 0, 0.205);
  border-bottom: 1px solid rgba(0, 0, 0, 0.205); */
  /* padding: 1vmax 0;
  color: rgba(0, 0, 0, 0.651);
  font: 400 1vmax "Roboto";
  margin: 1vmax 0;
  width: 92%; */
}

.detailsBlock-4 {
  color: black;
  margin-top: 15px;
}

.detailsBlock-4 > p {
  margin-top: 5px;
  color: black;
  font-size: 15px;
}

