.category {
  display: flex;
  justify-content: center;
  align-items: center;
  height: auto;
  width: 100%;
  color: #fff;
  margin: 2vh 0.8vw;
  font-size: 4em;
  background-color: white;
  text-decoration: none;
  /* border: solid 1px rgb(228, 228, 228); */
  /* box-shadow: 10px 10px 1px black; */
  border-radius: 15px;
  transition: all 0.5s;
  box-shadow: 0 0 4px rgba(54, 54, 54, 0.226);
}

.category:hover {
  box-shadow: 0 0 10px rgba(40, 40, 40, 0.226);
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
  transition: all 0.5s;
}

